<template>
  <page
    @on-ready="onReady"
    :headerHeightVW="headerHeightVW"
    :scrollbar="true"
    ref="page"
    class="organization-page"
  >

    <div slot="header">
      <van-nav-bar id="header" title="选择人员" :left-arrow="(departments && departments.length) ? true : false" @click-left="onClickLeft">
      </van-nav-bar>

      <div class="search-bar">
        <!-- <van-search placeholder="请输入搜索人员姓名" show-action>
          <div slot="action" @click="onSearch">搜索</div>
        </van-search>

        <div class="search-action">
          <i class="search-scan search-icon"></i>
        </div> -->

        <van-search
          placeholder="请输入搜索关键词"
          show-action
          shape="round"
          v-model="query"
        >
          <div slot="action" @click="onSearch">搜索</div>
        </van-search>
      </div>
    </div>
    

    <div class="list-container">
      <template v-if="queryShow">
        <van-cell-group>
          <van-cell @click="handleUser(item)" v-for="item in queryUserList" :key="item.id" :title="item.name" :label="item.description" clickable>

            <img class="user-icon" v-if="item.avator" :src="item.avator"  slot="icon">
            <img class="user-icon" v-else :src="defaultAvator" slot="icon">
          </van-cell>
        </van-cell-group>
      </template>
      <template v-else>
        <van-cell-group>
          <van-cell v-if="departmentShow"><span style="color: rgba(0,0,0,0.5);" v-html="departmentShow"></span></van-cell>
        </van-cell-group>
        
        <van-cell-group>
          <van-cell @click="handleDepartment(item)" v-for="item in departmentList" :key="item.id" :title="item.name" is-link clickable></van-cell>
        </van-cell-group>

        <van-cell-group>
          <van-cell @click="handleUser(item)" v-for="item in userList" :key="item.id" :title="item.name" :label="item.description" clickable>

            <img class="user-icon" v-if="item.avator" :src="item.avator"  slot="icon">
            <img class="user-icon" v-else :src="defaultAvator" slot="icon">
          </van-cell>
        </van-cell-group>
      </template>
      
    </div>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import io from '../../api'
import db from '../../db'
import {uploadImage, uploadVideo} from '../../utils/upload.js'

export default {
  components: {
    Page,
  },
  data: () => ({
    headerHeightVW: 0,
    userList: [],
    departmentList: [],
    departments: [{name: '联系人', id: ''}],
    id: null,
    mode: null,
    query: '',
    queryShow: false,
    queryUserList: [],
    defaultAvator: require('../../assets/images/avator-default.png'),
    type: 1,
    profession: '',
    status: ''
  }),
  computed: {
    departmentShow () {
      let strArray = []
      if (this.departments) {
        for (let i = 0; i < this.departments.length; i++) {
          strArray.push(this.departments[i].name)
        }
      }
      
      strArray[strArray.length - 1] = `<span style="color: #108EE9;">${strArray[strArray.length - 1]}</span>`
      return strArray.join(' > ')
    }
  },
  methods: {
    onReady (header, main) {
      this.headerHeightVW = (header.h + 57) * 100 / header.w

      if (navigate.getParam('departments')) {
        this.departments = navigate.getParam('departments')
      }
      this.id = navigate.getParam('id')
      this.mode = navigate.getParam('mode') || 'order'
      this.job = navigate.getParam('job') || ''
      this.profession = navigate.getParam('profession') || ''
      this.status = navigate.getParam('status') || ''

      if (navigate.getParam('type')) {
        this.type = navigate.getParam('type')
      }

      this.loadList()
    },
    loadList () {
      this.$toast.loading({
        duration: 0,
        loadingType: 'spinner'
      })

      // if (this.mode == 'problem') {
      //   io.getPendingReviewUser({
      //     id: this.id,
      //     profession: this.profession,
      //   }).then(data => {
      //     this.departmentList = data.departments
      //     this.userList = data.users
      //     this.$toast.clear()
      //   }).catch(msg => {
      //     this.$toast(msg)
      //   })
      // } else {
      //   io.getOrganization({
      //     department: this.departments.length > 0 ? this.departments[this.departments.length - 1].id : '',
      //     all: this.mode != 'order',
      //     job: this.job,
      //     profession: this.profession
      //   }).then(data => {
      //     this.departmentList = data.departments
      //     this.userList = data.users
      //     this.$toast.clear()
      //   }).catch(msg => {
      //     this.$toast(msg)
      //   })
      // }

      io.getOrganization({
        department: this.departments.length > 0 ? this.departments[this.departments.length - 1].id : '',
        all: this.mode != 'order',
        job: this.job,
        profession: this.profession,
        status: this.status
      }).then(data => {
        this.departmentList = data.departments
        this.userList = data.users
        this.$toast.clear()
      }).catch(msg => {
        this.$toast(msg)
      })
    },

    onSearch () {
      if (this.query) {
        // this.queryShow = true

        this.$toast.loading({
          duration: 0,
          loadingType: 'spinner'
        })

        io.searchOrganization({query: this.query}).then(data => {
          this.queryUserList = data.users
          this.$toast.clear()
        }).catch(msg => {
          this.$toast(msg)
        })
      } else {
        // this.queryShow = false
      }
    },

    handleDepartment (item) {
      let departmentsParam = [...this.departments]
      departmentsParam.push(item)

      let cid = []

      if (navigate.getParam('cid')) {
        cid = navigate.getParam('cid')
      }

      cid.push(item.id)

      navigate.copen('organization', item.id, {departments: departmentsParam, radom: (new Date()).getTime(), id: this.id, mode: this.mode, cid})
    },

    handleUser (item) {
      if (this.mode == 'problem_circulation') {
        localStorage.setItem('circulation_select', JSON.stringify(item))

        navigate.back('organization')

        if (navigate.getParam('cid')) {
          const cid = navigate.getParam('cid')

          for (let i = 0; i < cid.length; i++) {
            navigate.back('organization_'+cid[i])
          }
        }
      } else {
        this.$dialog.confirm({
          message: `已选择：${item.name}，是否确认转交`
        }).then(() => {
          console.log(item)
          this.$toast.loading({
            duration: 0,
            forbidClick: true,
            loadingType: 'spinner',
            message: '正在转发...'
          })

          if (this.mode == 'order') {
            // 同步本地数据
            db.getOrderDetail(this.id).then(data => {
              
              if (data.id == this.id) {
                // alert(data.id)
                // 上传图片资源
                uploadImage(data.signPhoto ? data.signPhoto.map(item => ({url: item})) : []).then(sp => {
                  data.signPhoto = sp

                  console.log('1')

                  if (this.type == 1) {
                    uploadImage(data.repairImages ? data.repairImages : []).then(s => {
                      console.log('2')
                      data.repairImages = s
                        // 上传视频
                        uploadVideo(data.repairVideos ? data.repairVideos : []).then(v => {
                          console.log('3')
                          data.repairVideos = v

                          io.uploadOrder(data).then(() => {
                            io.forwardOrder({
                              id: this.id,
                              userId: item.id
                            }).then(data => {
                              this.$toast.clear()
                              // 删除本地数据
                              db.removeOrder(this.id).then(() => {
                                navigate.back('organization')

                                if (navigate.getParam('cid')) {
                                  const cid = navigate.getParam('cid')

                                  for (let i = 0; i < cid.length; i++) {
                                    navigate.back('organization_'+cid[i])
                                  }
                                }
                              })
                            }).catch(msg => {
                              this.$toast(msg)
                            })
                          }).catch(msg => {
                            this.$toast(msg)
                          })
                        })
                    })
                  } else {
                    io.forwardPlan({
                        id: this.id,
                        userId: item.id
                      }).then(data => {
                        this.$toast.clear()
                        // 删除本地数据
                        db.removeOrder(this.id).then(() => {
                          navigate.back('organization')

                          if (navigate.getParam('cid')) {
                            const cid = navigate.getParam('cid')

                            for (let i = 0; i < cid.length; i++) {
                              navigate.back('organization_'+cid[i])
                            }
                          }
                        })
                      }).catch(msg => {
                        this.$toast(msg)
                      })
                  }

                })

              } else {
                // alert('---')
                io.forwardOrder({
                  id: this.id,
                  userId: item.id
                }).then(data => {
                  this.$toast.clear()
                  
                  navigate.back('organization')

                  if (navigate.getParam('cid')) {
                    const cid = navigate.getParam('cid')

                    for (let i = 0; i < cid.length; i++) {
                      navigate.back('organization_'+cid[i])
                    }
                  }
                }).catch(msg => {
                  this.$toast(msg)
                })
              }
            })
          }

          if (this.mode == 'problem') {
            io.forwardProblem({
              id: this.id,
              userId: item.id
            }).then(data => {
              this.$toast.clear()
              
              navigate.back('organization')

              if (navigate.getParam('cid')) {
                const cid = navigate.getParam('cid')

                for (let i = 0; i < cid.length; i++) {
                  navigate.back('organization_'+cid[i])
                }
              }
            }).catch(msg => {
              this.$toast(msg)
            })
          }
        }).catch(() => {})
      }
    },

    onClickLeft () {
      navigate.back()
    }
  },
  watch: {
    query (val) {
      if (val) {
        // this.queryUserList = []
        this.queryShow = true
      } else {
        this.queryShow = false
      }
    },
    queryShow (val) {
      if (val) {
        this.queryUserList = []
      }
    }
  }
}
</script>

<style lang="scss">
.list-container{
  .van-cell-group+.van-cell-group {
    margin-top: 12px;
  }

  .user-icon{
    width: 45px;
    height: 45px;
    margin-right: 12px;
    border-radius: 50%;
  }
}
</style>
